"use client"

import { useState } from "react"
import { useAuth } from "@/lib/auth-context"
import { LineCard } from "./line_card"

interface Bus {
  id: string
  plaque: string
  capacity: number
  status: "active" | "maintenance" | "inactive"
  driver: string
  city: string
  line: string
  lineId: string
}

interface BusLine {
  id: string
  name: string
  buses: Bus[]
}

// Mock data: All buses with their assigned drivers and cities
const ALL_BUSES_DATA = [
  // Casablanca - Ahmed Moussaoui
  {
    id: "b1",
    plaque: "CB-1001",
    capacity: 45,
    status: "active" as const,
    driver: "Ahmed Moussaoui",
    city: "CASABLANCA",
    line: "Ligne 1 - Centro",
    lineId: "l1",
  },
  // ... (le reste des données reste identique)
]

export function BusAssignmentView() {
  const { driver } = useAuth()
  const [expandedLines, setExpandedLines] = useState<Set<string>>(new Set())

  const toggleLine = (lineId: string) => {
    const newExpanded = new Set(expandedLines)
    newExpanded.has(lineId) ? newExpanded.delete(lineId) : newExpanded.add(lineId)
    setExpandedLines(newExpanded)
  }

  const driverBuses = ALL_BUSES_DATA.filter((b) => b.driver === driver.name)
  const driverCity = driver.city

  const busesByLine: Record<string, typeof ALL_BUSES_DATA> = {}
  driverBuses.forEach((bus) => {
    const key = bus.lineId
    if (!busesByLine[key]) {
      busesByLine[key] = []
    }
    busesByLine[key].push(bus)
  })

  const linesList = Object.entries(busesByLine).map(([lineId, buses]) => ({
    id: lineId,
    name: buses[0].line,
    buses: buses.map((b) => ({
      id: b.id,
      plaque: b.plaque,
      capacity: b.capacity,
      status: b.status,
      driver: b.driver,
    })),
  }))

  const stats = {
    total: driverBuses.length,
    active: driverBuses.filter((b) => b.status === "active").length,
  }

  return (
    <div className="p-8">
      <div className="max-w 5xl mx-auto w-full">
        {/* Header Section - Agrandie */}
        <div className="mb-10">
          <h1 className="text-4xl font-bold text-foreground mb-3">Bus Assignés</h1>
          <p className="text-lg text-muted-foreground mb-8">
            Gestion de vos bus assignés organisés par ligne
          </p>

          {/* Stats - Agrandies et améliorées */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-10">
            <div className="bg-gradient-to-r from-blue-50 to-blue-100 border border-blue-200 rounded-xl p-6 shadow-sm">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-blue-600 mb-1">Bus Totaux Assignés</p>
                  <p className="text-3xl font-bold text-blue-900">{stats.total}</p>
                </div>
                <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center">
                  <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
                  </svg>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-r from-emerald-50 to-emerald-100 border border-emerald-200 rounded-xl p-6 shadow-sm">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-emerald-600 mb-1">Bus En Service</p>
                  <p className="text-3xl font-bold text-emerald-900">{stats.active}</p>
                </div>
                <div className="w-12 h-12 bg-emerald-100 rounded-full flex items-center justify-center">
                  <svg className="w-6 h-6 text-emerald-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7" />
                  </svg>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-r from-purple-50 to-purple-100 border border-purple-200 rounded-xl p-6 shadow-sm">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-purple-600 mb-1">Lignes Assignées</p>
                  <p className="text-3xl font-bold text-purple-900">{linesList.length}</p>
                </div>
                <div className="w-12 h-12 bg-purple-100 rounded-full flex items-center justify-center">
                  <svg className="w-6 h-6 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
              </div>
            </div>
          </div>

          {/* Driver Info - Agrandie */}
          <div className="bg-gradient-to-r from-gray-50 to-gray-100 border border-border rounded-xl p-6 mb-8 shadow-sm">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-lg font-semibold text-foreground mb-2">Informations du Conducteur</h2>
                <div className="space-y-2">
                  <div className="flex items-center gap-4">
                    <div>
                      <p className="text-sm text-muted-foreground">Nom</p>
                      <p className="text-xl font-bold text-foreground">{driver.name}</p>
                    </div>
                    <div>
                      <p className="text-sm text-muted-foreground">Ville</p>
                      <p className="text-xl font-medium text-foreground">{driverCity}</p>
                    </div>
                  </div>
                  {driver.id && (
                    <div>
                      <p className="text-sm text-muted-foreground">ID Conducteur</p>
                      <p className="text-base font-medium text-foreground">{driver.id}</p>
                    </div>
                  )}
                </div>
              </div>
              <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center">
                <svg className="w-8 h-8 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              </div>
            </div>
          </div>
        </div>

        {/* Lines Section - Agrandie */}
        <div className="mb-8">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-foreground">Lignes Assignées</h2>
            <div className="text-sm text-muted-foreground">
              {linesList.length} ligne{linesList.length > 1 ? 's' : ''} assignée{linesList.length > 1 ? 's' : ''}
            </div>
          </div>

          {linesList.length === 0 ? (
            <div className="bg-gray-50 border-2 border-dashed border-gray-300 rounded-xl p-12 text-center">
              <svg className="w-16 h-16 text-gray-400 mx-auto mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
              </svg>
              <p className="text-lg text-gray-600 mb-2">Aucun bus assigné</p>
              <p className="text-gray-500">Contactez l'administration pour obtenir des affectations</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 gap-4">
              {linesList.map((line) => (
                <LineCard
                  key={line.id}
                  line={line}
                  isExpanded={expandedLines.has(line.id)}
                  onToggle={() => toggleLine(line.id)}
                />
              ))}
            </div>
          )}
        </div>

        {/* Additional Info Section */}
        {linesList.length > 0 && (
          <div className="bg-gradient-to-r from-blue-50 to-indigo-50 border border-blue-200 rounded-xl p-6 mt-8">
            <h3 className="text-lg font-semibold text-blue-900 mb-3">Informations Complémentaires</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="flex items-start gap-3">
                <svg className="w-5 h-5 text-blue-600 mt-0.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                <div>
                  <p className="font-medium text-blue-800">Capacité Totale</p>
                  <p className="text-blue-700">
                    {driverBuses.reduce((sum, bus) => sum + bus.capacity, 0)} passagers maximum
                  </p>
                </div>
              </div>
              <div className="flex items-start gap-3">
                <svg className="w-5 h-5 text-blue-600 mt-0.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                <div>
                  <p className="font-medium text-blue-800">Disponibilité</p>
                  <p className="text-blue-700">
                    Tous les bus sont actifs et prêts au service
                  </p>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}